<script lang="ts">
  import { analytics } from "@lib/analytics"
  import { Paths, useRouter } from "@lib/router"
  import source, { type ImageSource } from "../best-match/stores/image-source"
  import * as sca from "../face-analysis/state"
  import ImageSourceSelector from "./ImageSourceSelector"
  import addPhotoPng from "./img/add-photo.png"
  import analysisPng from "./img/analysis.png"
  import arrowBottomSvg from "./img/arrow-bottom.svg"
  import arrowTopSvg from "./img/arrow-top.svg"
  import giftPng from "./img/gift.png"

  const router = useRouter()

  let src: ImageSource = null

  $: src && ($source = src)
  $: src && router.push(Paths.GiftSelection)
</script>

<div class="tint-image-source-gift">
  <h1>Pick up a gift for your friend</h1>
  <p>Upload a photo with the person for whom youâ€™re choosing a gift.</p>
  <ul>
    <li>
      <img src={addPhotoPng} alt="Add" />
      <p>Add a photo</p>
      <img class="arrow-top" src={arrowTopSvg} alt="Next" />
    </li>
    <li>
      <img src={analysisPng} alt="Analysis" />
      <p>Analysis</p>
      <img class="arrow-bottom" src={arrowBottomSvg} alt="Next" />
    </li>
    <li>
      <img src={giftPng} alt="Pick up a gift" />
      <p>Pick up a gift</p>
    </li>
  </ul>
  <ImageSourceSelector
    on:webcam={({ detail: granted }) => {
      granted
        ? (sca.clear(), (src = { type: "webcam" }))
        : alert("Please, grant the camera permission to use this feature")
    }}
    on:photo={({ detail: photo }) => (sca.clear(), (src = { type: "photo", data: photo }))}
    on:photo={() => analytics.photo_uploaded()}
    on:webcam={({ detail: granted }) => {
      analytics.live_camera_clicked()
      analytics.camera_permission(granted)
    }}
    isGift={true}
  />
</div>

